
class terminal {
    static pid;
    static history;

    constructor() {
        this.pid = 0;
        this.history = [];
        console.log('Created new terminal');
    }

    getPid = () => {
        return this.pid++;
    }

    runCommand = (cmd) => {
        const splitCmd = cmd.split(/ /);
        const exec = splitCmd[0];
        const args = splitCmd.slice(1);
        let stdout;

        switch (exec) {
            case 'echo':
                stdout = args.join(' ').replace(/^['"]/, '').replace(/['"]$/, '')
                break;
            case 'whoami':
                stdout = 'A Colorado-based full-stack developer'
                break;
            default:
                stdout = `jsh: command not found: ${exec}`
                break;
        }
        const result = command(cmd, stdout, this.getPid())
        this.history.push(result)
        return result
    }
}

const command = (stdin, stdout, pid) => ({
    stdin: stdin,
    stdout: stdout,
    pid: pid
})

module.exports = terminal;
